.comment {
  margin-bottom: 23px;

  @media (min-width: $width-tablet) {
    margin-bottom: 65px;
  }

  @media (min-width: $width-desktop) {
    margin-bottom: 70px;
  }
}

.comment__title {
  @include form-title;
  margin-bottom: 38px;

  @media (min-width: $width-tablet) {
    margin-bottom: 87px;
  }

  @media (min-width: $width-desktop) {
    margin-bottom: 78px;
  }
}

.comment__field {
  @include form-main-text;
  width: 100%;
  padding: 14px;
  border: 1px solid #e7e7e7;

  @media (min-width: $width-tablet) {
    padding: 11px 20px;
    height: 175px;
    line-height: 30px;
  }

  @media (min-width: $width-desktop) {
    padding: 9px 20px;
  }
}
